import { useState, useEffect, useCallback } from 'react';
import { Table, Space } from 'antd';
import { EditOutlined, DeleteOutlined } from '@ant-design/icons';

const PROJECT_URL =
  'https://aiwinops-default-rtdb.firebaseio.com/projects.json';

const TABLE_COLUMN = [
  {
    title: '專案名稱',
    key: 'name',
    dataIndex: 'name',
  },
  {
    title: '狀態',
    key: 'status',
    dataIndex: 'status',
  },
  {
    title: '管理者',
    key: 'manager',
    dataIndex: 'manager',
  },
  {
    title: '建立日期',
    key: 'build_time',
    dataIndex: 'build_time',
  },
  {
    title: '修改日期',
    key: 'modify_time',
    dataIndex: 'modify_time',
  },
  {
    title: '其他操作',
    key: 'editable',
    dataIndex: 'editable',
    render: (editable) => {
      return (
        // 連結Icon到各自專案的編輯畫面
        <Space>
          {editable && <EditOutlined style={{ fontSize: '150%' }} />}{' '}
          <DeleteOutlined style={{ fontSize: '150%' }} />
        </Space>
      );
    },
  },
];

const ProjectList = (props) => {
  const [projectData, setProjectData] = useState();

  // 抓取使用者專案的資料 -> GET/ProjectData
  const fetchProjectData = useCallback(() => {
    fetch(PROJECT_URL)
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        const storeData = [];
        for (const key in data) {
          storeData.push({
            key: key,
            name: data[key].name,
            status: data[key].status,
            manager: data[key].manager,
            build_time: data[key].build_time,
            modify_time: data[key].modify_time,
            editable: data[key].editable,
          });
        }
        setProjectData(storeData);
        console.log(projectData);
      })
      .catch((error) => {
        throw new Error(error);
      });
  }, []);

  useEffect(() => {
    fetchProjectData();
  }, [fetchProjectData]);

  return (
    <div>
      <Table
        scroll={{ x: '1000', y: '500' }}
        pagination={false}
        columns={TABLE_COLUMN}
        dataSource={projectData}
      />
    </div>
  );
};

export default ProjectList;
